import styled from "styled-components";
import { Component } from "react";

/*
 * Animation for revealing a section when scrolled onto.. CSS (1st phase) Setup our CSS
 */

const Revealable = styled.section`
  opacity: ${({ show }) => (show ? "100" : "0")};
  margin-left: ${({ show }) => (show ? "0" : "100px")};
  transition: opacity ease-in 2s, margin-left cubic-bezier(1, 0, 0.4, 1) 2s;
`;

const RevealableRight = styled.section`
  opacity: ${({ show }) => (show ? "100" : "0")};
  margin-right: ${({ show }) => (show ? "0" : "100px")};
  transition: opacity ease-in 2s, margin-right cubic-bezier(1, 0, 0.4, 1) 2s;
`;

const RevealableOnly = styled.section`
  opacity: ${({ show }) => (show ? "100" : "0")};
  transition: opacity ease-in 3s;
`;

const RevealableOnly2 = styled.section`
  opacity: ${({ show }) => (show ? "100" : "0")};
  transition: opacity ease-in 6s;
`;

const Reveal1 = styled.section`
  opacity: ${({ show }) => (show ? "100" : "0")};
  margin-left: ${({ show }) => (show ? "0" : "50px")};
  transition: opacity cubic-bezier(1, 0.5, 0.4, 1) 1s,
    margin-left cubic-bezier(0.3, 0, 0.4, 1) 1s;
`;
const Reveal2 = styled.section`
  opacity: ${({ show }) => (show ? "100" : "0")};
  margin-left: ${({ show }) => (show ? "0" : "100px")};
  transition: opacity cubic-bezier(1, 0.5, 0.4, 1) 1s,
    margin-left cubic-bezier(0.5, 0, 0.4, 1) 1.5s;
`;
const Reveal3 = styled.section`
  opacity: ${({ show }) => (show ? "100" : "0")};
  margin-left: ${({ show }) => (show ? "0" : "150px")};
  transition: opacity cubic-bezier(1, 0.5, 0.4, 1) 1s,
    margin-left cubic-bezier(0.5, 0, 0.4, 1) 2s;
`;
const Reveal4 = styled.section`
  opacity: ${({ show }) => (show ? "100" : "0")};
  margin-left: ${({ show }) => (show ? "0" : "200px")};
  transition: opacity cubic-bezier(1, 0.5, 0.4, 1) 1s,
    margin-left cubic-bezier(0.5, 0, 0.4, 1) 2.5s;
`;
const Reveal5 = styled.section`
  opacity: ${({ show }) => (show ? "100" : "0")};
  margin-left: ${({ show }) => (show ? "0" : "250px")};
  transition: opacity cubic-bezier(1, 0.5, 0.4, 1) 1s,
    margin-left cubic-bezier(0.5, 0, 0.4, 1) 3s;
`;
const Reveal6 = styled.section`
  opacity: ${({ show }) => (show ? "100" : "0")};
  margin-left: ${({ show }) => (show ? "0" : "300px")};
  transition: opacity cubic-bezier(1, 0.5, 0.4, 1) 1s,
    margin-left cubic-bezier(0.5, 0, 0.4, 1) 3.5s;
`;

export {
  RevealableRight,
  RevealableOnly,
  RevealableOnly2,
  Reveal1,
  Reveal2,
  Reveal3,
  Reveal4,
  Reveal5,
  Reveal6
};

export default Revealable;
